.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
button {
  box-shadow: 2px 2px 4px rgba(1, 1, 24, 0.5); /* Adjust the color and spread radius as needed */
  transition: box-shadow 0.28s; /* Only need to specify the property being transitioned */
  cursor: pointer;
}

button:active {
  transform: translate(2px, 2px); /* Adjust the translation values as needed */
  box-shadow: 0px 0px 0px rgba(255, 255, 255, 0); /* Adjust the color and spread radius as needed */
}
.timer{
  background: #d49494;
  font-size: x-large;
  /* font-stretch: inherit; */
  font-family: -webkit-body;
  color: aliceblue;
  border: 1px solid white;
  border-radius: 5px;
  box-shadow: 4px 4px 4px 1px #798282;
  padding: 3px;

}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
